<template>
  <div class="side-icons">
    <div class="circle-icon" @click="toTop">
      <i class="iconfont icon-fanhuidingbu" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'SideIcons',
  data() {
    return {}
  },
  methods: {
    toTop() {
      var timer = setInterval(function() {
        const osTop = document.documentElement.scrollTop || document.body.scrollTop
        const ispeed = Math.floor(-osTop / 5)
        document.documentElement.scrollTop = document.body.scrollTop = osTop + ispeed
        if (osTop === 0) {
          clearInterval(timer)
        }
      }, 30)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~@/assets/styles/variables.scss';
.side-icons{
  right: 20px;
  bottom: 10%;
  z-index: 2001;
  position: fixed;
  .circle-icon{
    width: 50px;
    height: 50px;
    color: #fff;
    line-height: 50px;
    text-align: center;
    border-radius: 50%;
    background-color: rgba($color: #666, $alpha: .6);
  }
}
</style>
